.js-imgover {
  position: relative;
}

.js-imgover__item {
  width: 80%;
  transform-style: preserve-3d;
  perspective: 2000px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 99;
  display: none;
}

.js-imgover__item.is-open {
  display: block;
}

.js-imgover__close {
  width: 60px;
  height: 60px;
  position: fixed;
  top: 50px;
  right: 50px;
  display: none;
  z-index: 100;
  cursor: pointer;
}

.js-imgover__close::before, .js-imgover__close::after {
  content: "";
  width: 120px;
  height: 1px;
  background: #fff;
  position: absolute;
  left: calc( 50% - ( 120px / 2 ));
  display: block;
  transition: transform 0.2s;
}

.js-imgover__close::before {
  transform: rotate(45deg);
  top: 28px;
}

.js-imgover__close::after {
  transform: rotate(-45deg);
  top: 28px;
}

.js-imgover__close.is-open {
  display: block;
}

.js-imgover__bg {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background: #000;
  opacity: .8;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 98;
  display: none;
}

.js-imgover__bg.is-open {
  display: block;
}

